.todo-form {
  width: 100%;
  padding: 2rem 2.2rem;
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 2rem;

  &__circle {
    width: 2.5rem;
    height: 2.5rem;
    border: 1px solid var(--color-todo-done-text);
    border-radius: 10rem;
    display: inline-block;
  }
}
